.c-section-header {
  position: relative;
  background: #32968F;
  padding-top: 1.4375rem;
  text-align: center;
}

.c-section-header::before,
.c-section-header::after {
  position: absolute;
  content: "";
  bottom: -2.25rem;
}

.c-section-header::before {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 50vw 37px 0;
  border-color: transparent #32968F transparent transparent;
  left: 0;
}

.c-section-header::after {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 37px 50vw 0 0;
  border-color: #32968F transparent transparent transparent;
  right: 0;
}

.c-section-header__title {
  position: relative;
  display: inline-block;
  color: #fff;
  font-size: 1.5625rem;
  font-weight: 900;
  line-height: 1.47;
  z-index: 1;
}
@media screen and (min-width: 768px) {
  .c-section-header__title {
    font-size: 2.125rem;
  }
}

.c-section-header__title br {
  line-height: 0;
}

.c-section-header__figure {
  position: relative;
  display: inline-block;
  transform: skewX(-35deg);
  width: 1.875rem;
  height: 1.875rem;
  margin: 0 auto;
  background-color: #2FB1A7;
  left: 0;
  bottom: -0.5rem;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .c-section-header__figure {
    transform: skewX(-40deg);
    width: 3.75rem;
    height: 2.9375rem;
